import React from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import TextField from "@material-ui/core/TextField";
import CardContent from "@material-ui/core/CardContent";
import {
  Box,
  Breadcrumbs,
  Link,
  Button,
  Grid,
  MenuItem,
  Typography,
  FormControl,
  InputLabel,
  Select,
} from "@material-ui/core";
import { Formik } from "formik";
import axios from "axios";
import Design from "../Design";
import { useEffect, useState } from "react";
import toast from "react-hot-toast";
import userService from "../apis/userService";
import { Section } from "../HelperFunction";
import { useLocation, useNavigate } from "react-router-dom";
import {customAxios} from "../apis/Axios";

const useStyles = makeStyles((theme) => ({
  head: {
    // padding:"20px",\
    height: "100vh",
    paddingRight: 20,
    paddingLeft: 20,
    PaddingTop: 10,
    background: "none",
  },
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "275",
      margin: "17px",
      minWidth: 275,
    },
  },
  title: {
    color: "black",
  },
}));

const AssignTeacher = () => {
  const classes = useStyles();
  const { state } = useLocation();
  const Navigate = useNavigate();
  const [classObj, setClassObj] = useState([]);
  const [teacher, setTeacher] = useState([]);
  const [Session, setSession] = useState([]);
  const [sectionobj, setSection] = useState([]);

  console.group(state);
  if ((state == null)) {
    var invoice = {
      className: "",
      first_name: "",
      id:"" ,
      sectionName: "",
      yearName: "",
    };
  }
  else{
    state.invoice;
    var {invoice}=state;
  }

  const Teacher = async () => {
    const teacherData = await customAxios.getCall(
      "teacher/teachers/api"
    );
    setTeacher(teacherData);
  };

  const session = async () => {
    const sessionData = await customAxios.getCall(
      "class/session"
    );
    setSession(sessionData);
  };

  useEffect(() => {
    const fun = Section("class/api");
    fun.then((res) => {
      setClassObj(res);
    });
    const fun1 = Section("class/api/section");
    fun1.then((res) => {
      setSection(res);
    });
    Teacher();
    session();
  }, []);
  return (
    <>
      <div>
        <Card className={classes.head}>
          <Formik
            initialValues={{
              yearid: invoice.yearid || "",
              teacherid: invoice.teacherid || "",
              classid: invoice.classid || "",
              sectionid: invoice.sectionid || "",
            }}
            validationSchema={Yup.object().shape({
              yearid: Yup.string().max(255).required(),
            })}
            onSubmit={async (values) => {
              // alert(JSON.stringify(values, null));
              // const data = { ...values };
              try {
                customAxios
                  .postCall("class/addassignteacher", {
                    yearid: values.yearid,
                    teacherid: values.teacherid,
                    classid: values.classid,
                    sectionid: values.sectionid,
                  })
                  .then((res) => {
                    // console.log("res", res);
                    res.json;
                  });
                // console.log(values);
                toast.success("Teacher Assign  SuccessFully");
                Navigate("../Classes");
              } catch (err) {
                console.log(err);
              }
            }}
          >
            {({
              handleReset,
              dirty,
              handleChange,
              handleSubmit,
              isSubmitting,
              values,
            }) => (
              <div>
                <Design
                  text="Assign Teacher"
                  dashText="Assign Teacher to class"
                />
                <Box style={{ paddingLeft: 20 }} boxShadow={20} color="black">
                  <form onSubmit={handleSubmit} autoComplete="off">
                    <div>
                      {/* <Box boxShadow={20} color="transparent"> */}
                      <CardContent>
                        <div className={classes.title}>
                          <Typography variant="h6">
                            Search Student Promotion
                          </Typography>
                        </div>
                      </CardContent>
                      <div>
                        {" "}
                        <FormControl style={{ margin: 8, width: "95%" }}>
                          <InputLabel id="yearid">Year</InputLabel>

                          <Select
                            fullWidth
                            labelId="yearid"
                            value={values.yearid}
                            onChange={handleChange}
                            id="yearid"
                            label="Year"
                            name="yearid"
                            // multiple
                          >
                            {Session.map((option) => (
                              <MenuItem key={option.id} value={option.id}>
                                {option.year}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                        <FormControl style={{ margin: 8, width: "95%" }}>
                          <InputLabel id="teacherid">Teacher </InputLabel>

                          <Select
                            fullWidth
                            labelId="teacherid"
                            value={values.teacherid}
                            onChange={handleChange}
                            id="teacherid"
                            label="Teacher"
                            name="teacherid"
                          >
                            {teacher.map((option) => (
                              <MenuItem key={option.id} value={option.id}>
                                {`${option.first_name}`}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                        <FormControl style={{ margin: 8, width: "95%" }}>
                          <InputLabel id="classid">Class</InputLabel>

                          <Select
                            fullWidth
                            labelId="classid"
                            value={values.classid}
                            onChange={handleChange}
                            id="classid"
                            label="classid"
                            name="classid"
                          >
                            {classObj.map((option) => (
                              <MenuItem key={option.id} value={option.id}>
                                {option.className}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                        <FormControl style={{ margin: 8, width: "95%" }}>
                          <InputLabel id="sectionid">Section</InputLabel>

                          <Select
                            fullWidth
                            labelId="sectionid"
                            value={values.sectionid}
                            onChange={handleChange}
                            id="sectionid"
                            label="Section"
                            name="sectionid"
                          >
                            {sectionobj.map((option) => (
                              <MenuItem key={option.id} value={option.id}>
                                {option.name}
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                      </div>
                      <Button
                        style={{ margin: "17px" }}
                        variant="contained"
                        size="medium"
                        color="success"
                        type="submit"
                        disabled={isSubmitting}
                        disableElevation
                      >
                        Save
                      </Button>
                      <Button
                        style={{ margin: "17px" }}
                        variant="contained"
                        size="medium"
                        color="primary"
                        onClick={handleReset}
                        disabled={!dirty || isSubmitting}
                      >
                        Reset
                      </Button>
                      {/* </Box> */}
                    </div>
                  </form>
                </Box>
              </div>
            )}
          </Formik>
        </Card>
      </div>
    </>
  );
};

export default AssignTeacher;
